.header {
  background-color: getColor(heroBgrd);
}

.header__button {
  margin-left: auto;
}

.button-icon {
  width: 4px;
  height: 8px;
  fill: getColor(accent);
}

.header .container {
  display: inline-flex;
  align-items: center;
  padding-top: 38px;
  padding-bottom: 38px;
}

.main-nav {
  display: flex;
  align-items: center;
}

.site-nav {
  padding: 0;
  margin: 0;
}

.site-nav.list {
  display: flex;
}

.site-nav__link {
  text-decoration: none;
  font-family: $main-font;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.31;
  margin: 15px 35px 15px auto;

  color: getColor(btncolor);

  &:hover,
  &:focus {
    color: getColor(accent);
  }
}

.header__button {
  height: 44px;
  padding: 0 36px;
  border-radius: 22px;
  border: none;
  background-color: getColor(btncolor);
  color: getColor(accent);
  font-family: $main-font;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.31;
}

// .logo-image {
//     width: 199px;
//     height: 50px;
//     display: block;
//     margin-right: 40px;
// }
.logo-image {
  display: block;
  width: 199px;
  height: 100%;
  margin-right: 140px;
}

* {
  outline: 1px solid blue;
}

.mobile-nav {
  padding: 15px 20px;
  width: 250px;
  height: 100vh;
  background-color: getColor(bgcmobile);
}

.mobile-nav .site-nav {
  display: inline-block;
}

.mobile-nav .site-nav__item {
  margin-right: 0;
  margin-bottom: 22px;
}
